import React, { Component } from 'react'
import ApplicationPositions from './ApplicationPositions/ApplicationPositions';
import { ApplicationStepsTypes } from './ApplicationSteps/ApplicationSteps';
import PositionRequirement from './PositionRequirement/PositionRequirement';
import ExamCenter from './ExamCenter/ExamCenter';
import * as Interfaces from '../../interfaces/index'
import { connect } from "react-redux";
import {
  Auth,
  examDataInterface,
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers,
  initiateApplication,
  selectPosition,
  createApplication,
  setApplicationStep,
  positionsStoreInterface,
  FC_Access,
} from "../../actions";
import { StoreState } from "../../reducers";
import { applicationStoreInterface, initialApplicationInterface } from '../../actions/application';
import { ApplicationInterface } from '../../interfaces/application';
import { PositionAPI } from '../../interfaces/index';
import { Redirect } from 'react-router';
import { UserAccessList } from '../../utils/auth_access';

interface ApplicationState {
	currentStep: ApplicationStepsTypes;
  currentPosition: Interfaces.PositionAPI | null;
  redirectState: boolean;
}

interface ApplicationProps {
  auth: Auth;
  application: applicationStoreInterface;
  initiateApplication: (initial: initialApplicationInterface) => void;
  createApplication: (user_id: string, application: ApplicationInterface) => void
  setApplicationStep: (step: ApplicationStepsTypes) => void;
  selectPosition: (position: PositionAPI | null) => void;
  FC_Access: Function;
}

export class _Application extends Component<ApplicationProps, ApplicationState> {
  constructor(props: ApplicationProps) {
		super(props);

		this.state = {
      currentStep: ApplicationStepsTypes.Step1,
      currentPosition: null,
      redirectState: false,
		};
	}
  componentDidMount = () => {
    // this.props.initiateApplication()
    if (this.props.application.apply === undefined) {
      this.props.initiateApplication({
        user_id: this.props.auth.user.user_id, 
        selectedPosition: null, 
        currentStep: ApplicationStepsTypes.Step1,
        positions: null,
        doTestStatus: {
          status: false,
        },
        selectedLocation: []
      });
      if (this.props.application.applications.length > 0 && this.props.application.apply === undefined) {
        this.setState({ redirectState: true })
      } else {
        this.setState({ redirectState: false })
      }
    }
    console.log("Application: ", this.props.application);
  }
  render() {
    // if (this.props.application.applications.length > 0 && this.props.application.apply === undefined) {
    //   return <Redirect to="/applications" />
    // }
    if (!FC_Access(this.props.auth, UserAccessList.MY_APPLICATION).create) {
      return <Redirect to="/" />
    }
    return (
      this.props.application.apply === undefined ? <Redirect to="/applications" /> : 
      <div className="pr-2">
        {/* Step 2 */}
        {
        this.props.application.apply !== undefined &&
        this.props.application.apply.currentStep === ApplicationStepsTypes.Step2 ? (
          // <DoTest 
          //   currentPosition={this.props.application.apply.selectedPosition}
          //   setCurrentPosition={this.props.selectPosition}
          //   setApplicationStep={this.props.setApplicationStep}
          // />
          <div>Do psychometric tex</div>
        ) : this.props.application.apply !== undefined && (
          <div className="">
            {this.props.application.apply.selectedPosition !== null && this.props.application.apply.currentStep === ApplicationStepsTypes.Step1 ? "" :
            <div className="mt-2 pt-2 pb-1 border-b-2">
              <div className="flex flex-col">
                <span className="text-blue-600 text-2xl lg:text-3xl font-bold mb-1">Create the application</span>
                {this.props.application.apply.selectedPosition !== null ?
                <div className="flex flex-row items-center gap-3 text-sm text-gray-600"><div className="text-blue-200 font-bold bg-blue-700 rounded px-2 border border-blue-300">{this.props.application.apply.currentStep === ApplicationStepsTypes.Step1 ? "Step 1 of 2:" : "Step 2 of 2"}</div> Choosing examination center</div>
                :<span className="text-gray-600 text-xs lg:text-sm">List of positions ready for the application, choose a position</span>}
              </div>
            </div>}

            <div className="my-2">
              <div className="w-full">
              {/* Step one */}
              {this.props.application.apply.currentStep === ApplicationStepsTypes.Step1 && (
                this.props.application.apply.selectedPosition === null ? (
                  <ApplicationPositions
                    currentPosition={this.props.application.apply.selectedPosition}
                    setCurrentPosition={this.props.selectPosition}
                  />
                ) : (
                  <PositionRequirement
                    currentPosition={this.props.application.apply.selectedPosition}
                    setCurrentPosition={this.props.selectPosition}
                    setApplicationStep={this.props.setApplicationStep}
                  />
                ))}
                {/* Step 3 */}
                {this.props.application.apply.currentStep === ApplicationStepsTypes.Step3 && (
                  <ExamCenter 
                    currentPosition={this.props.application.apply.selectedPosition}
                    setCurrentPosition={this.props.selectPosition}
                    setApplicationStep={this.props.setApplicationStep}
                  />
                )}
              </div>
            </div>
          </div>
        )}
        
      </div>
    )
  }
}

const mapStateToProps = ({
  auth,
  doExam,
  application,
  positions,
}: StoreState): { auth: Auth; doExam: examDataInterface; application: applicationStoreInterface; positions: positionsStoreInterface; } => {
  return { auth: auth, doExam: doExam, application: application, positions };
};

export const Application = connect(mapStateToProps, {
  loadExam,
  addExamAnswer,
  deleteExamAnswers,
  SubmitExamAnswers,
  initiateApplication,
  selectPosition,
  createApplication,
  setApplicationStep,
  FC_Access,
})(_Application);