import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { MdLocationOn, MdSearch } from 'react-icons/md';
import Loading from '../../../components/Loading/Loading';
import { course } from '../../../interfaces/course';
import { LocationAPI } from '../../../interfaces/location';
import { API } from '../../../utils/api';
import searchData from "../../../utils/search";

export interface examRoomInterface {
  isUsed: boolean;
  room_type: string;
  room_number: number;
  capacity: number;
  examination_center_room_id: string;
}

export interface LocationTableInterface {
  province_id: string;
  province_name: string;
  district_id: string;
  district_name: string;
  center_id: string;
  center_name: string;
  rooms: examRoomInterface[];
  capacity: number;
}

interface ChooseExamCenterProps {
  addLocation: (
    course_id: string, 
    course_name: string,
    course_code: string,
    location: LocationTableInterface,
    exam_id: string,
  ) => void;
  positionCourses: course;
  closeModal: () => any;
}

export const findCenterCapacity = (rooms: examRoomInterface[]) => {
  let sum = 0;
  for (let i in rooms) {
    sum = sum + rooms[i].capacity;
  }
  return sum;
}

export const LocationTable = (LocationData: LocationAPI[]): LocationTableInterface[] | [] => {
  let locationTemp: LocationTableInterface[] = [];
  for (let p in LocationData) {
    let districts = LocationData[p].districts;
    for (let d in districts) {
      let centers = districts[d].centers;
      for (let c in centers) {
        let rooms = centers[c].rooms;
        let capacity = 0;
        for (let x in rooms) {
          capacity = capacity + (rooms[x].capacity - rooms[x].occupied);
        }
        locationTemp.push({
          province_id: LocationData[p].province_id,
          province_name: LocationData[p].province_name,
          district_id: districts[d].district_id,
          district_name: districts[d].district_name,
          center_id: centers[c].examination_center_id,
          center_name: centers[c].center_name,
          rooms: rooms,
          capacity: capacity,
        })
      }
    }
  }
  return locationTemp;
}

const ChooseExamCenter = (props: ChooseExamCenterProps) => {
  const [search, setSearch] = useState<string | "">("");
  const [locations, setLocations] = useState<LocationAPI[] | [] | null>(null);
  const [selectedProvince, setSelectedProvince] = useState<string | null>(null);
  
  const LoadLocations = async () => {
    try {
      const res = await axios.get<LocationAPI[]>(`${API.APPLICATION}/roomlocation`);
      if (res.status === 200) {
        if (locations === null || locations.length === 0) {
          setLocations(res.data);
        }
      }
      console.log("Locations: ", locations);
    } catch (error: any) {
      console.log("Locations: ", {...error});
    }
  }

  useEffect(() => {
    LoadLocations();
  })
  return (
    <div className="w-full">
      <div className="flex flex-row items-center">
        <div>
          <MdLocationOn className="text-6xl text-blue-500" />
        </div>
        <div className="flex flex-col justify-start">
          <span className="text-2xl font-bold text-primary">Exam: {props.positionCourses.course_code}-{props.positionCourses.course_name}</span>
          <span className="text-sm text-gray-500">Choose location for the exam</span>
        </div>
      </div>

      <div className="flex flex-row px-3 gap-3 my-3 items-center w-full">
        <div><span className="text-gray-600 text-sm">Filter by location</span></div>
        <div className="flex flex-col md:flex-row gap-3 w-2/3">
          <select onChange={(e: React.ChangeEvent<HTMLSelectElement>) => setSelectedProvince(e.target.value)} className="input-white bg-white border border-gray-500 w-1/3">
            <option value="">Select province</option>
            {locations !== null && locations.length > 0 && locations.map((item, i) => (
              <option key={i + 1} value={item.province_id}>{item.province_name}</option>
            ))}
          </select>
          <input onChange={(e: React.ChangeEvent<HTMLInputElement>) => setSearch(e.target.value)} type="text" placeholder="District or center name" className="input-white bg-white border border-gray-500 2/3" />
        </div>
      </div>

      <div className="w-full overflow-x-auto">
        <div className="hidden md:flex flex-row gap-3 w-full justify border-b-2 border-blue-600 py-4 bg-blue-100 text-blue-900 items-center">
          <div className="w-1/12 text-center font-bold">#</div>
          <div className="w-1/4 font-bold">Province</div>
          <div className="w-1/3 font-bold">District</div>
          <div className="w-1/3 font-bold">Center</div>
          <div className="w-1/12 font-bold">Capacity</div>
          <div className="w-1/12 font-bold"></div>
        </div>
        {locations === null ? <div className="pt-6"><Loading /></div> :
          locations.length === 0 ? 
          <div className="flex flex-col justify-center items-center text-gray-400 pt-4">
            <MdSearch className="text-7xl" />
            <span className="text-3xl">No result found</span>
          </div> :
          searchData(selectedProvince !== null ? LocationTable(locations).filter(itm => itm.province_id === selectedProvince) : LocationTable(locations), search).map((item, i) => (
            <div 
            onClick={() => {
              props.addLocation(
                props.positionCourses.course_id, 
                props.positionCourses.course_name, 
                props.positionCourses.course_code, 
                item,
                props.positionCourses.course_id
              ); 
              props.closeModal();
            }} key={i + 1} className="flex flex-col md:flex-row gap-3 rounded md:rounded-none bg-gray-50 px-3 mx-2 md:mx-0 md:px-0 md:bg-white mb-2 md:mb-0 justify-start border md:border-0 md:border-b text-gray-700 border-gray-300 py-1 text-left items-center cursor-pointer hover:bg-blue-600 hover:text-white text-sm">
              <div className="w-full md:w-1/12 text-left md:text-center font-bold">
                <div className="hidden md:block">{i + 1}</div>
                <div className="bg-blue-100 text-blue-700 rounded-full w-8 h-8 flex md:hidden items-center justify-center mt-2">{i + 1}</div>
              </div>
              <div className="w-full md:w-1/4">{item.province_name}</div>
              <div className="w-full md:w-1/3">{item.district_name}</div>
              <div className="w-full md:w-1/3">{item.center_name}</div>
              <div className="w-full md:w-1/12">{item.capacity}</div>
              <div className="w-full md:w-1/12 px-3">
                {props.positionCourses !== undefined &&
                  <button className="h-full px-2 py-1 float-right bg-blue-100 text-blue-700 hover:bg-blue-600 hover:border-blue-700 hover:text-white border rounded border-blue-400">Choose</button>}
              </div>
            </div>
          ))}
      </div>
    </div>
  )
}

export default ChooseExamCenter
